.ButtonlessFilters ul {
  -webkit-padding-start: 0px;
  -webkit-margin-after: 0em;
}

.ButtonlessFilters li {
  display: inline-block;
  margin: 0 18px 6px 0;
  font-size: 1.17em;
  border-bottom: 3px solid transparent;
}

.ButtonlessFilters .selected{
  border-bottom: 3px solid #d1212e;
}



